<template>
  <view class="container">
    <view class="login-form">
      <view class="logo-section">
        <text class="logo-text">宝宝起名</text>
        <text class="logo-desc">给宝宝一个美好的名字</text>
      </view>

      <view class="form-item">
        <view class="form-label">用户名</view>
        <input
          class="form-input"
          v-model="formData.username"
          placeholder="请输入用户名"
          @input="handleInput"
        />
      </view>

      <view class="form-item">
        <view class="form-label">密码</view>
        <input
          class="form-input"
          v-model="formData.password"
          type="password"
          placeholder="请输入密码"
          @input="handleInput"
        />
      </view>

      <button class="btn-primary" @click="handleLogin" :disabled="loading">
        {{ loading ? '登录中...' : '登录' }}
      </button>

      <view class="register-link" @click="toggleMode">
        {{ isLoginMode ? '没有账号？点击注册' : '已有账号？点击登录' }}
      </view>
    </view>
  </view>
</template>

<script setup>
import { ref, reactive } from 'vue'
import { useUserStore } from '@/store'
import { userApi } from '@/api/user'

const userStore = useUserStore()
const loading = ref(false)
const isLoginMode = ref(true)

const formData = reactive({
  username: '',
  password: ''
})

const handleInput = () => {
  // 处理输入
}

const handleLogin = async () => {
  if (!formData.username || !formData.password) {
    uni.showToast({
      title: '请填写完整信息',
      icon: 'error'
    })
    return
  }

  loading.value = true

  try {
    const apiMethod = isLoginMode.value ? userApi.login : userApi.register
    const result = await apiMethod(formData)

    if (result.success) {
      userStore.setToken(result.data.token)
      userStore.setUserInfo(result.data.userInfo)

      uni.showToast({
        title: isLoginMode.value ? '登录成功' : '注册成功',
        icon: 'success'
      })

      setTimeout(() => {
        uni.switchTab({
          url: '/pages/baby-info/index'
        })
      }, 1000)
    } else {
      uni.showToast({
        title: result.message || '操作失败',
        icon: 'error'
      })
    }
  } catch (error) {
    console.error('登录失败:', error)
    uni.showToast({
      title: '网络错误，请重试',
      icon: 'error'
    })
  } finally {
    loading.value = false
  }
}

const toggleMode = () => {
  isLoginMode.value = !isLoginMode.value
  formData.username = ''
  formData.password = ''
}

// 不需要 return，setup 语法糖会自动暴露
</script>

