  text-align: center;
  margin-bottom: 80rpx;
}

.logo-text {
  font-size: 48rpx;
  font-weight: bold;
  color: #007aff;
  display: block;
  margin-bottom: 20rpx;
}

.logo-desc {
  font-size: 28rpx;
  color: #999;
}

.register-link {
  text-align: center;
  color: #007aff;
  font-size: 28rpx;
  margin-top: 40rpx;
}

.register-link:active {
  opacity: 0.7;
}
</style>
